<script>
import Vue from "vue";
import { NavBar, Toast, Icon, Row, Col } from "vant";

Vue.use(NavBar);
Vue.use(Toast);
Vue.use(Icon);
Vue.use(Row).use(Col);
export default {
  data() {
    return {
      title: "devil_zds",
      show_nav: false,
      maxScrollTop: 0,
      scrollTop: 0,
      opacity: 0,
      intro_list: [
          {
              type: 'education',
              icon: 'fire',
              text: '教育背景'
          },
           {
              type: 'jobIntention',
              icon: 'fire',
              text: '求职意向'
          },
           {
              type: 'workExperience',
              icon: 'fire',
              text: '工作经历'
          },
           {
              type: 'skill',
              icon: 'fire',
              text: '技能'
          }
      ]
    };
  },
  methods: {
    handleScroll() {
      let self = this;
      self.maxScrollTop =
        self.$refs.banner.offsetHeight +
        self.$refs.person_info.offsetHeight -
        // self.$refs.intro.offsetHeight -
        30;
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      self.scrollTop = scrollTop;
    },
    intro_jump(type) {
        switch(type) {
            case 'education':
            window.scrollTo(0, this.$refs.education.offsetTop - 46);
            break;
            case 'jobIntention':
            window.scrollTo(0, this.$refs.jobIntention.offsetTop - 46);
            break;
            case 'workExperience':
            window.scrollTo(0, this.$refs.workExperience.offsetTop - 46);
            break;
            case 'skill':
            window.scrollTo(0, this.$refs.skill.offsetTop - 46);
            break;
        }
    }
  },
  watch: {
    scrollTop(val) {
      let self = this;
      if (val > 0) {
        self.show_nav = true;
        self.opacity = val / self.maxScrollTop;
      } else {
        self.show_nav = false;
        self.opacity = 0;
      }
    }
  },
  created() {},
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  }
};
</script>

